<template>
    <div>
        <div class="row">
            <div class="col s12 m7 l7">
                <div class="card">
                    <div class="card-title center" style="background-color: rgb(24, 105, 149)">
                        <p class="white-text center-block">Mortgage Rate Survey</p>
                    </div>
                    <div class="card-content">
                        <div class="row">
                            <div class="col s12 m8 l8">
                                <label>Current Province/Territory of residence</label>
                                <select v-model="select_province" @change="onChangeValue"
                                        id="select_province" class="browser-default">
                                    <option value="0" disabled selected>Chosen Province</option>
                                    <option value="AB" selected>Alberta, AB</option>
                                    <option value="BC" selected>British Columbia, BC</option>
                                    <option value="MB" selected>Manitoba, MB</option>
                                    <option value="NB" selected>New Brunswick, NB</option>
                                    <option value="NL" selected>Newfoundland & Labrador, NL</option>
                                    <option value="NT" selected>Northwest Territories, NT</option>
                                    <option value="NS" selected>Nova Scotia, NS</option>
                                    <option value="NU" selected>Nunavut, NU</option>
                                    <option value="ON" selected>Ontario, ON</option>
                                    <option value="PE" selected>Prince Edward Island, PE</option>
                                    <option value="QC" selected>Quebec, QC</option>
                                    <option value="SK" selected>Saskatchewan, SK</option>
                                    <option value="YT" selected>Yukon, YT</option>
                                </select>
                            </div>
                        </div>
                        <div class="row center">
                            <div class="col s12 m12 l4">
                                <label class="label" for="select_term_value">
                                    Term Value
                                </label>
                                <select class="browser-default" v-model="select_term_value" @change="onChangeValue"
                                        id="select_term_value">
                                    <option value="0" disabled>Chosen Term Value</option>
                                    <option value="0.5" default>6 Months</option>
                                    <option value="1">1 Year</option>
                                    <option value="2">2 Year</option>
                                    <option value="3">3 Year</option>
                                    <option value="4">4 Year</option>
                                    <option value="5">5 Year</option>
                                    <option value="10">10 Year</option>
                                </select>

                                <div class="card-panel hide-on-small-only">
                                    <h6>{{select_term_value}}
                                        Years</h6>
                                </div>
                            </div>
                            <div class="col s12 m12 l4">

                                <label for="select_open_closed">
                                    Open/Closed
                                </label>
                                <select class="browser-default " v-model="select_open_closed" @change="onChangeValue"
                                        id="select_open_closed">
                                    <option value="0" disabled>Chosen Open/Closed</option>
                                    <option value="O" selected>Open</option>
                                    <option value="C" selected>Closed</option>
                                </select>

                                <div class="card-panel hide-on-small-only ">
                                    <h6 v-if='select_open_closed=="O"'>Open</h6>
                                    <h6 v-if='select_open_closed=="C"'>Closed</h6>
                                </div>
                            </div>
                            <div class="col s12 m12 l4">
                                <label for="select_rate_type">
                                    Rate Type
                                </label>
                                <select class="browser-default" v-model="select_rate_type" @change="onChangeValue"
                                        id="select_rate_type">
                                    <option value="0" disabled>Chosen Rate Type</option>
                                    <option value="F" selected>Fixed</option>
                                    <option value="V" selected>Variable</option>
                                </select>
                                <div class="card-panel hide-on-small-only">
                                    <h6 v-if='select_rate_type=="F"'>Fixed</h6>
                                    <h6 v-if='select_rate_type=="V"'>Variable</h6>
                                </div>
                            </div>
                            <div class="col s12 m12 l12">
                                <div v-if="loading" class="preloader-wrapper big active">
                                    <div class="spinner-layer spinner-blue-only">
                                        <div class="circle-clipper left">
                                            <div class="circle"></div>
                                        </div>
                                        <div class="gap-patch">
                                            <div class="circle"></div>
                                        </div>
                                        <div class="circle-clipper right">
                                            <div class="circle"></div>
                                        </div>
                                    </div>
                                </div>
                                <div v-if='companies!=null && errored==false'>
                                    <h6 class="blue-text">Lowest rate</h6>
                                    <div class="card center z-depth-4">
                                        <div class="card-content">
                                            <span class="card-title">{{companies[0].company_desc_en}}</span>
                                            <p>{{companies[0].interest_rate_value}} %</p>
                                        </div>
                                        <div class="card-action">
                                            <button class="btn btn-small" style="background-color: rgb(202, 209, 71)">
                                                INQUIRE
                                                NOW
                                            </button>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col s12 m5 l5">
                <muchmoney :minrate="minrate"></muchmoney>
            </div>
        </div>
        <producties :companies="companies" :errored="errored" :is204="is204" :loading="loading"></producties>
    </div>
</template>

<script>
    import CryptoJS from 'crypto-js';
    import Axios from 'axios';
    import Muchmoney from "./Muchmoney";
    import Producties from "./Producties";

    export default {
        name: "Insurfact",
        components: {Producties, Muchmoney},
        data: () => ({
            keySize: 128,
            iterationCount: 1000,
            loading: true,
            errored: false,
            err: null,
            is204: false,
            crypt_data: null,
            companies: null,
            select_term_value: '4',
            select_open_closed: 'C',
            select_rate_type: 'F',
            select_province: 'QC',
            minrate: 0
        }),
        mounted() {
            this.getData()
        },
        methods: {
            getData: function () {
                const uri = "https://wsemortgage.insurfact.com/wsemortgage/webresources/mortgage1/"
                let param = this.select_term_value + '/' + this.select_open_closed + '/' + this.select_rate_type + '/' + this.select_province + '/' + '2'
                Axios
                    .get(uri + encodeURIComponent(this.encrypt(param, 'insurfact')))
                    .then(response => {
                        if (response.status == 200) {
                            this.crypt_data = response.data.response
                            this.getValues()
                        } else if (response.status == 204) {
                            this.is204 = true
                            this.minrate = 0
                        }

                    })
                    .catch(error => {
                        this.errored = true
                        this.err = error
                    })
                    .finally(() => {
                        this.loading = false
                    })
            },
            getValues: function () {
                let array_encrypt = this.crypt_data.split(':')
                this.companies = JSON.parse(this.decrypt(array_encrypt[0], array_encrypt[1], array_encrypt[2], 'insurfact').toString())
                this.minrate = this.companies[0].interest_rate_value
            },
            onChangeValue: function () {
                this.loading = true
                this.errored = false
                this.err = null
                this.companies = null
                this.is204 = false
                this.getData()
            },
            encrypt: function (plainText, passPhrase) {
                let iv = CryptoJS.lib.WordArray.random(this.keySize / 8).toString(CryptoJS.enc.Hex);
                let salt = CryptoJS.lib.WordArray.random(this.keySize / 8).toString(CryptoJS.enc.Hex);
                let key = this.generateKey(salt, passPhrase)
                let encrypted = CryptoJS.AES.encrypt(
                    plainText,
                    key,
                    {iv: CryptoJS.enc.Hex.parse(iv)});
                let a = encrypted.ciphertext.toString(CryptoJS.enc.Base64)
                let paramrequest = salt + ":" + iv + ":" + a;
                return (paramrequest.toString())
            },
            decrypt: function (salt, iv, encryptedText, passPhrase) {
                let key = this.generateKey(salt, passPhrase);
                let cipherParams = CryptoJS.lib.CipherParams.create({
                    ciphertext: CryptoJS.enc.Base64.parse(encryptedText)
                });
                let decrypted = CryptoJS.AES.decrypt(
                    cipherParams,
                    key,
                    {iv: CryptoJS.enc.Hex.parse(iv)});
                return decrypted.toString(CryptoJS.enc.Utf8);
            },
            generateKey: function (salt, passPhrase) {
                var key = CryptoJS.PBKDF2(
                    passPhrase,
                    CryptoJS.enc.Hex.parse(salt),
                    {keySize: this.keySize / 32, iterations: this.iterationCount});
                return key;
            }
        }
    }

</script>