<template>
    <div class="card z-depth-1">
        <div class="card-title center" style="background-color: rgb(202, 209, 71)">
            <p class="white-text">Select a product</p>
        </div>
        <div class="card-content center">
            <div v-if="loading" class="preloader-wrapper big active">
                <div class="spinner-layer spinner-blue-only">
                    <div class="circle-clipper left">
                        <div class="circle"></div>
                    </div>
                    <div class="gap-patch">
                        <div class="circle"></div>
                    </div>
                    <div class="circle-clipper right">
                        <div class="circle"></div>
                    </div>
                </div>
            </div>
            <h4 class="center red-text" v-if="errored==true">{{err}}. An unexpected
                error has
                occurred.
                Try
                again later </h4>
            <div v-if='companies!=null && errored==false'>
                <div class="row">
                    <div v-for="i in n" :key="i">
                        <tarjeta :company="companies[i-1]" :key="companies[i-1].company_desc_en"></tarjeta>
                    </div>
                    <!--<tarjeta v-for="company in companies" :key="company.company_desc_en" :company="company"></tarjeta>-->
                </div>
                <a v-on:click="expandList" class="waves-effect waves-teal btn-flat">{{textobutton}}</a>
            </div>
            <div v-if="is204">
                <h4 class="center">There is no information to show.</h4>
            </div>
        </div>
    </div>
</template>

<script>
    import Tarjeta from "./Tarjeta";

    export default {
        name: "Producties",
        components: {Tarjeta},
        data: () => ({
            n: 6,
            textobutton: 'Expand list'
        }),
        props: ['companies', 'errored', 'is204', 'loading'],
        methods: {
            expandList: function () {
                if (this.n == 6) {
                    this.textobutton = 'Contract list'
                    this.n = this.companies.length
                } else {
                    this.textobutton = 'Expand list'
                    this.n = 6
                }
            }
        }
    }
</script>

<style scoped>

</style>