<template>
    <div>
        <div class="card center">
            <div class="card-title" style="background-color: rgb(202, 209, 71)">
                <p class="grey-text text-darken-4">How much money do you need</p>
            </div>
            <div class="card-content">
                <div class="card-panel" style="background-color: rgb(202, 209, 71)">
                    <div id="msform">
                        <h6 class="blue-text">Entry the Loans</h6>
                        <input class="insur_input" type="number" v-model="loans" placeholder="Loans"/>
                    </div>
                </div>
            </div>
        </div>
        <div class="card-panel center" style="background-color: rgb(24, 105, 149)">
            <div v-if="minrate>0">
                <h4 class="white-text">Your quote</h4>
                <h5 class="white-text truncate">${{getQuote}}</h5>
                <button class="btn waves-effect waves-light">Per Month</button>
            </div>
            <div v-else>
                <hp class="white-text">You cannot calculate your quote with these data</hp>
            </div>
            <br>
            <div class="container">
                <h6 class="center white-text" for="amort">
                    Amortization Period
                </h6>
                <select class="browser-default" v-model="amortization" id="amort">
                    <option value="0" disabled>Chosen Amortization Period</option>
                    <option value="5">5 Years</option>
                    <option value="10">10 Year</option>
                    <option value="15">15 Year</option>
                    <option value="20">20 Year</option>
                    <option value="25">25 Year</option>
                </select>
            </div>

        </div>
    </div>
</template>

<script>
    export default {
        name: "Muchmoney",
        props: ['minrate'],
        data: () => ({
            amortization: 25,
            loans: 100000
        }),
        computed: {
            getc3: function () {
                return (this.minrate / 100) / 12
            },
            getb3: function () {
                return Math.pow(1 + this.getc3, 12 * this.amortization)
            },
            getd3: function () {
                return this.getb3 - 1
            },
            getQuote: function () {
                return Math.round(this.loans * this.getc3 * this.getb3 / this.getd3 * 100) / 100
            }
        }
    }
</script>
<style scoped>
    #msform fieldset {
        background: white;
        border: 0 none;
        border-radius: 3px;
        box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
        padding: 20px 30px;
        box-sizing: border-box;
        width: 80%;
        margin: 0 10%;

        /*stacking fieldsets above each other*/
        position: relative;
    }

    /*Hide all except first fieldset*/
    #msform fieldset:not(:first-of-type) {
        display: none;
    }

    /*inputs*/
    #msform .insur_input {
        background: white;
        padding: 15px;
        border: 1px solid #ccc;
        border-radius: 3px;
        margin-bottom: 10px;
        width: 100%;
        box-sizing: border-box;
        font-family: montserrat;
        color: #2C3E50;
        font-size: 13px;
    }
</style>